const websiteUrl = baseIp + 'api/';

const addBaseUrl = function(url) {

	if(url == undefined) 
		return "";
	if (url.startsWith("http")) 
		return url;
	if(url.length > 4) {
		console.log(url.substring(0,4));
		let prefix = url.substring(0,4);
		if(prefix == 'http') {
			return url;
		} 
	}

	return baseIp + url;

}
