const now = Date.now || function() {
	return new Date().getTime();
};

/**
 * 
 */
const arrayToString = function(array) {
	let data = "";
	array.forEach((item) => {
		data = data + item + ",";
	});

	if (data.length > 0) {
		return data.substring(0, data.length - 1);
	}
	return data;
};
