/**
 * ImgStyle max-width:100%
 * @param {Object} html
 */
const resizeImg = function(html) {
	html = html.replace(/style\s*?=\s*?(["])[\s\S]*?\1/ig, '') // styl

	let str = html.replace(/<img[^>]*>/gi, function(match, capture) {
		let img = match.replace(/style\s*?=\s*?(["])[\s\S]*?\1/ig, '') // styl
		return img.replace(/<img/ig, '<img style="max-width:100%;height:auto;" ');
	})

	return str;
}

const deleteHtmlTag = function(str) {
	str = str.replace(/<[^>]+>|&[^>]+;/g, "").trim(); //html&nbsp;
	return str;
}


/**
 * 
 */
const dateUtils = {
	UNITS: {
		'': 31557600000,
		'': 2629800000,
		'': 86400000,
		'': 3600000,
		'': 60000,
		'': 1000
	},

	humanize: function(milliseconds) {
		var humanize = '';
		for (var key in this.UNITS) {
			if (milliseconds >= this.UNITS[key]) {
				humanize = Math.floor(milliseconds / this.UNITS[key]) + key + '';
				break;
			}
		}
		return humanize || '';
	},
	format: function(dateStr) {
		var date = this.parseDate(dateStr)
		var diff = Date.now() - date.getTime();
		if (diff < this.UNITS['']) {
			return this.humanize(diff);
		}
		var _format = function(number) {
			return (number < 10 ? ('0' + number) : number);
		};
		return date.getFullYear() + '/' + _format(date.getMonth() + 1) + '/' + _format(date.getDay()) + '-' +
			_format(date.getHours()) + ':' + _format(date.getMinutes());
	},
	parseDate: function(str) { //"yyyy-mm-dd HH:MM:ss"Date
		var a = str.split(/[^0-9]/);
		return new Date(a[0], a[1] - 1, a[2], a[3], a[4], a[5]);
	},
	parseString: function(Timestamp) { //,"yyyy-mm-dd HH:MM"
		var now = new Date(Timestamp),
			y = now.getFullYear(),
			m = now.getMonth() + 1,
			d = now.getDate();
		return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 5);
	},
	parseStringBySecond: function(Timestamp) { //,"yyyy-mm-dd HH:MM:ss"
		var now = new Date(Timestamp),
			y = now.getFullYear(),
			m = now.getMonth() + 1,
			d = now.getDate();
		return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d) + " " + now.toTimeString().substr(0, 8);
	},
	parseStringByDay: function(Timestamp) { //,"yyyy-mm-dd"
		var now = new Date(Timestamp),
			y = now.getFullYear(),
			m = now.getMonth() + 1,
			d = now.getDate();
		return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
	},
	parseStringByDay1: function(Timestamp) { //,"yyyy-mm"
		var now = new Date(Timestamp),
			y = now.getFullYear(),
			m = now.getMonth() + 1;
		return y + "-" + (m < 10 ? "0" + m : m);
	},
	parseStringByDayAdd: function(Timestamp,day) { //,"yyyy-mm-dd"
		var now = new Date(Timestamp),
		now = now.setDate(now.getDate()+day),
		now = new Date(now),
			y = now.getFullYear(),
			m = now.getMonth() + 1,
			d = now.getDate();
		return y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
	},
	parseStringByHour: function(Timestamp) { //,"HH:MM"
		var now = new Date(Timestamp),
			y = now.getFullYear(),
			m = now.getMonth() + 1,
			d = now.getDate();
		return  now.toTimeString().substr(0, 5);
	},
	formatTime: function(dateStr) {
		if(!dateStr){
			dateStr=dateUtils.parseStringByDay(new Date());
		}
		return (new Date(Date.parse(dateStr.replace(/-/g,"/")))).getTime() / 1000;
	},
};

/**
 * 
 * @param {Object} list
 * @param {Object} keys
 */
const setTime = function(list, keys) {
	list.forEach((item) => {
		keys.forEach((key) => {
			item[key] = dateUtils.parseString(item[key])
		});
	});
	return list;
}


const formatTime = function(longTime) {
	return dateUtils.parseString(longTime);
}


